import {
	classNames,
	widgetSettings,
	getInitials,
	base64ToString
} from './../../lib/utils';
import { action, observable } from 'mobx';
import { IWidgetSetting, showAllMode } from '../app/app';
import { observer } from 'mobx-react';
import _ from './../../translate';
import r3e, { registerUpdate, unregisterUpdate } from '../../lib/r3e';
import React from 'react';
import ReconnectingWebSocket from '../../lib/reconnecting-websocket';
import style from './crewChief.scss';

interface IProps extends React.HTMLAttributes<HTMLDivElement> {
	settings: IWidgetSetting;
}

@observer
export default class CrewChief extends React.Component<IProps, {}> {
	@observable
	driverName = '';

	@observable
	isActive = false;

	pingInterval: number;

	cachedNames = {};
	ws: ReconnectingWebSocket;

	constructor(props: IProps) {
		super(props);

		registerUpdate(this.update);

		this.ws = new ReconnectingWebSocket('ws://localhost:8071/crewchief');
		this.ws.reconnectInterval = 10000;
		this.ws.onmessage = (e) => {
			const data: {
				channelOpen: boolean;
			} = JSON.parse(e.data);
			this.setActive(data.channelOpen);
		};

		this.pingInterval = setInterval(() => {
			// Each time we send the server a message it will respond with the data
			if (this.ws.readyState !== WebSocket.OPEN) {
				return;
			}
			this.ws.send('');
		}, 100);
	}

	componentWillUnmount() {
		this.ws.close();
		clearInterval(this.pingInterval);
		unregisterUpdate(this.update);
	}

	formatName(name: string) {
		if (this.cachedNames[name]) {
			return this.cachedNames[name];
		}
		this.cachedNames[name] = name
			.toString()
			.replace(/(.)[^ ]*? (.*)/, '$1. $2');
		return this.cachedNames[name];
	}

	@action
	private update = () => {
		const driverLength = r3e.data.DriverData.length;
		if (this.driverName === '') {
			for (let i = 0; i < driverLength; i++) {
				if (
					r3e.data.VehicleInfo.SlotId ===
					r3e.data.DriverData[i].DriverInfo.SlotId &&
					r3e.data.BrakeRaw !== -1
					) {
					this.driverName =
						getInitials(
							base64ToString(
								r3e.data.DriverData[i]
								.DriverInfo.Name
							)
						);
					break;
				}
			}
		}
	};

	@action
	private setActive = (active: boolean) => {
		this.isActive = active;
	};

	render() {
		if (!this.isActive && !showAllMode) {
			return null;
		}

		return (
			<div
				{...widgetSettings(this.props)}
				className={classNames(style.crewChief, this.props.className)}
			>
				<div className="driverName">
					<div className="inner">
						{showAllMode
							?	'N. Lauda'
							:	this.driverName}
					</div>
				</div>
				<div className="meta">
					{_('Team Radio')}
					<img
						className="waveForm"
						src={require('./../../img/crewchief.gif')}
					/>
				</div>
			</div>
		);
	}
}
