import { action, observable } from 'mobx';
import { classNames, widgetSettings, INVALID } from './../../lib/utils';
import { IAidSettings } from './../../types/r3eTypes';
import { IWidgetSetting, lowPerformanceMode, showAllMode } from '../app/app';
import { observer } from 'mobx-react';
import r3e, { registerUpdate, unregisterUpdate } from '../../lib/r3e';
import React from 'react';
import style from './aids.scss';
import SvgIcon from '../svgIcon/svgIcon';

interface IProps extends React.HTMLAttributes<HTMLDivElement> {
	settings: IWidgetSetting;
}

@observer
export default class Aids extends React.Component<IProps, {}> {
	@observable
	sessionType = INVALID;

	@observable
	aids: IAidSettings = {
		// ABS; -1 = N/A, 0 = off, 1 = on, 5 = active
		Abs: INVALID,
		// TC; -1 = N/A, 0 = off, 1 = on, 5 = active
		Tc: INVALID,
		// ESP; -1 = N/A, 0 = off, 1 = low, 2 = medium, 3 = high, 5 = active
		Esp: INVALID,
		// Countersteer; -1 = N/A, 0 = off, 1 = on, 5 = active
		Countersteer: INVALID,
		// Cornering; -1 = N/A, 0 = off, 1 = on, 5 = active
		Cornering: INVALID
	};

	@observable
	lastCheck = 0;

	@observable
	playerSlotId = -1;

	@observable
	isPlayer = false;

	constructor(props: IProps) {
		super(props);

		registerUpdate(this.update);
	}

	componentWillUnmount() {
		unregisterUpdate(this.update);
	}

	@action
	private update = () => {
		const nowCheck = Date.now();
		if (
			!lowPerformanceMode ||
			nowCheck - this.lastCheck >= 66.66
		) {
			this.aids = r3e.data.AidSettings;
			this.lastCheck = nowCheck;
			if (this.playerSlotId === -1 && r3e.data.SteerLockDegrees > 0) {
				this.playerSlotId = r3e.data.VehicleInfo.SlotId;
			}
			this.isPlayer = this.playerSlotId !== -1 &&
				r3e.data.VehicleInfo.SlotId === this.playerSlotId;
		}
	};

	render() {
		if (
			!this.isPlayer ||
			(
				this.aids.Abs < 1 &&
				this.aids.Esp < 1 &&
				this.aids.Tc < 1 &&
				this.aids.Countersteer < 1 &&
				this.aids.Cornering < 1 &&
				!showAllMode
			)
		) {
			return null;
		}
		return (
			<div
				{...widgetSettings(this.props)}
				className={classNames(style.aids, this.props.className)}
			>
				<div className="inner">
					{(this.aids.Abs > 0 || showAllMode) && (
						<div
							className={classNames(
								'aid abs',
								`state-${this.aids.Abs}`
							)}
						>
							<SvgIcon
								src={require('./../../img/icons/abs.svg')}
							/>
						</div>
					)}
					{(this.aids.Esp > 0 || showAllMode) && (
						<div
							className={classNames(
								'aid esp',
								`state-${this.aids.Esp}`
							)}
						>
							<SvgIcon
								src={require('./../../img/icons/esp.svg')}
							/>
						</div>
					)}
					{(this.aids.Tc > 0 || showAllMode) && (
						<div
							className={classNames(
								'aid tc',
								`state-${this.aids.Tc}`
							)}
						>
							<SvgIcon
								src={require('./../../img/icons/tc.svg')}
							/>
						</div>
					)}
					{(this.aids.Countersteer > 0 || showAllMode) && (
						<div
							className={classNames(
								'aid countersteer',
								`state-${this.aids.Countersteer}`
							)}
						>
							<SvgIcon
								src={require('./../../img/icons/countersteer.svg')}
							/>
						</div>
					)}
					{(this.aids.Cornering > 0 || showAllMode) && (
						<div
							className={classNames(
								'aid cornering',
								`state-${this.aids.Cornering}`
							)}
						>
							<SvgIcon
								src={require('./../../img/icons/cornering.svg')}
							/>
						</div>
					)}
				</div>
			</div>
		);
	}
}
