import {
	classNames,
	INVALID,
	widgetSettings
} from './../../lib/utils';
import { action, observable } from 'mobx';
import { ESession, ICutTrackPenalties } from './../../types/r3eTypes';
import { IWidgetSetting, lowPerformanceMode, showAllMode } from '../app/app';
import { observer } from 'mobx-react';
import _ from './../../translate';
import r3e, {
	registerUpdate,
	unregisterUpdate,
	nowCheck
} from './../../lib/r3e';
import React from 'react';
import style from './info.scss';
import SvgIcon from '../svgIcon/svgIcon';

interface IProps extends React.HTMLAttributes<HTMLDivElement> {
	settings: IWidgetSetting;
}

@observer
export default class Info extends React.Component<IProps, {}> {
	@observable
	currentLapValid = INVALID;

	@observable
	sessionType = INVALID;

	@observable
	completedLaps = 0;

	@observable
	lapTimeBestSelf = INVALID;

	@observable
	fuelPercent = INVALID;

	@observable
	penalties: ICutTrackPenalties = {
		DriveThrough: 0,
		StopAndGo: 0,
		PitStop: 0,
		TimeDeduction: 0,
		SlowDown: 0
	};

	penaltyTexts = {
		DriveThrough: 'DriveThrough: Serve wihin 3 laps'
	};

	@observable
	notInRace = false;

	@observable
	hasValidLap = true;

	@observable
	showLapInvalid = true;

	@observable
	lastCheck = 0;

	constructor(props: IProps) {
		super(props);

		registerUpdate(this.update);
	}

	componentWillUnmount() {
		unregisterUpdate(this.update);
	}

	@action
	private update = () => {
		if (
			(
				!lowPerformanceMode &&
				nowCheck - this.lastCheck >= 133.32
			) ||
			nowCheck - this.lastCheck >= 266.64
		) {
			this.lastCheck = nowCheck;
			this.currentLapValid = r3e.data.CurrentLapValid;
			this.sessionType = r3e.data.SessionType;
			this.penalties = r3e.data.Penalties;
			this.completedLaps = r3e.data.CompletedLaps;
			this.lapTimeBestSelf = r3e.data.LapTimeBestSelf;
			this.fuelPercent = (r3e.data.FuelLeft / r3e.data.FuelCapacity) * 100;
			this.notInRace = this.sessionType !== ESession.Race;
			this.hasValidLap =
				(this.currentLapValid > 0 && this.completedLaps > 0) ||
				(this.sessionType === ESession.Qualify &&
					this.lapTimeBestSelf === INVALID);
			this.showLapInvalid = this.notInRace && !this.hasValidLap;

		}
	};

	render() {
		return (
			<div
				className={classNames(style.info, this.props.className)}
				{...widgetSettings(this.props)}
			>
				{(this.showLapInvalid || showAllMode) && (
					<div className="warning">
						<SvgIcon
							src={require('./../../img/icons/warning.svg')}
						/>
						{_('This lap will not count')}
					</div>
				)}
				{(this.fuelPercent < 10 || showAllMode) && (
					<div className="warning">
						<SvgIcon
							src={require('./../../img/icons/warning.svg')}
						/>
						{_('Fuel at')}: {`${showAllMode
						?	5
						:	this.fuelPercent.toFixed(1)}%`}
					</div>
				)}

				{/* Loop through all penalties and check if they should show */}
				{Object.keys(this.penalties)
					.filter((penaltyKey) => this.penalties[penaltyKey] > 0)
					.map((penaltyKey) => {
						return (
							<div key={penaltyKey} className="warning">
								<SvgIcon
									src={require('./../../img/icons/warning.svg')}
								/>
								{this.penaltyTexts[penaltyKey] || penaltyKey}
							</div>
						);
					})}
			</div>
		);
	}
}
