import {
	classNames,
	base64ToString,
	fancyTimeFormatGap,
	formatTime,
	getCarName,
	getCarClassName,
	getInitials,
	/*showDebugMessage,*/
	widgetSettings,
	INVALID,
	getClassColor
} from './../../lib/utils';
import {
	ESession,
	IDriverData,
	ESessionPhase
} from './../../types/r3eTypes';
import {
	IWidgetSetting,
	lowPerformanceMode,
	showAllMode,
	eDriverNum
} from '../app/app';
import { action, observable } from 'mobx';
import { observer } from 'mobx-react';
import { personalBestTime, eRoundsLeft } from '../fuelDetail/fuelDetail';
import { times, uniq } from 'lodash-es';
import _ from './../../translate';
import r3e, {
	registerUpdate,
	unregisterUpdate,
	nowCheck
} from '../../lib/r3e';
import React from 'react';
import style from './positionBar.scss';

interface IProps extends React.HTMLAttributes<HTMLDivElement> {
	relative: boolean;
	settings: IWidgetSetting;
}
interface IDriverInfo {
	isUser: boolean;
	id: number;
	modelId: number;
	name: string;
	position: number;
	positionClass: number;
	meta: IDriverData;
	diff: string | number;
	lapDiff: number;
	classColor: string;
	carPerformance: number;
	carClassId: number;
	carClassName: string;
	inPit: number;
	mandatoryPit: number;
	numStops: number;
	validLap: number;
	currentTime: number;
	gapToPlayer: number;
	finishStatus: number;
	userId: number;
	lapDistance: number;
	logoUrl: string;
	classUrl: string;
}
@observer
export default class PositionBar extends React.Component<IProps, {}> {
	@observable
	vrGame = false;

	@observable
	drivers: IDriverInfo[] = [];

	@observable
	driversAhead: IDriverInfo[] = [];

	@observable
	driversBehind: IDriverInfo[] = [];

	@observable
	allDrivers: IDriverInfo[] = [];

	@observable
	currentLap = INVALID;

	@observable
	maxLaps = INVALID;

	@observable
	pitState = INVALID;

	@observable
	sessionPhase = INVALID;

	@observable
	sessionTimeRemaining = INVALID;

	@observable
	classDriverCount = INVALID;

	@observable
	positionClass = INVALID;

	@observable
	position = INVALID;

	@observable
	sessionType = INVALID;

	@observable
	multiClass = false;

	@observable
	lapTimeCurrentSelf = INVALID;

	@observable
	playerCount = INVALID;

	@observable
	playersAhead = INVALID;

	@observable
	playersBehind = INVALID;

	@observable
	classesAndCount = [ [], [], [] ];

	@observable
	lastCheck = 0;

	@observable
	playerSlotId = -1;

	@observable
	isPlayer = false;

	@observable
	actualRoundsLeft = -1;

	playerPosition = INVALID;

	positionBarCount = 15;

	entryWidth = 148;

	userDriverData: IDriverInfo | null = null;

	classColorUpdate: number;

	logoUrlp1 = 'http://game.raceroom.com/store/image_redirect?id=';
	logoUrlp2 = '&size=small';

	constructor(props: IProps) {
		super(props);

		registerUpdate(this.update);

		this.forceClassColorUpdate();
		this.classColorUpdate = setInterval(
			this.forceClassColorUpdate,
			10 * 1000
		);
	}
	componentWillUnmount() {
		clearInterval(this.classColorUpdate);
		unregisterUpdate(this.update);
	}

	private sortByLapDistance = (a: IDriverInfo, b: IDriverInfo) => {
		return b.gapToPlayer - a.gapToPlayer;
	};

	private sortByPerformance = (a: IDriverInfo, b: IDriverInfo) => {
		return b.carPerformance - a.carPerformance;
	};

	@action
	private update = () => {
		if (
			(
				!lowPerformanceMode &&
				nowCheck - this.lastCheck >= 133.32
			) ||
			nowCheck - this.lastCheck >= 266.64
		) {
			this.lastCheck = nowCheck;
			this.pitState = r3e.data.PitState;

			if (this.playerSlotId === -1 && r3e.data.SteerLockDegrees > 0) {
				this.playerSlotId = r3e.data.VehicleInfo.SlotId;
			}
			this.isPlayer = this.playerSlotId !== -1 &&
				r3e.data.VehicleInfo.SlotId === this.playerSlotId;

			/* if (this.props.relative &&
				this.props.settings.subSettings.showClassAndCounts.enabled) {
				let allDrivers = r3e.data.DriverData.map(this.formatDriverData);
				allDrivers = allDrivers.sort(this.sortByPerformance);
				this.classesAndCount = [ [], [], [] ];
				let count = allDrivers.reduce((drivers, i) => drivers + (i === 'a'), 0);
			} */

			this.classDriverCount = 0;
			this.playersAhead = 0;
			this.playersBehind = 0;
			this.playerCount = r3e.data.DriverData.length;
			this.multiClass = false;

			let driverData = this.props.relative
				? this.props.settings.subSettings.showAllSessions.enabled
					? r3e.data.DriverData.map(
							this.formatDriverData
						).filter(
							this.filterDriverDataQualy
						)
					: r3e.data.DriverData.map(
							this.formatDriverData
						).filter(
							this.filterDriverData
						)
				: r3e.data.DriverData.map(
							this.formatDriverData
						).filter(
							this.filterDriverData
						);

			// Deal with filtering and ordering relative positions
			if (this.props.relative) {
				if (this.props.settings.subSettings.showAllSessions.enabled) {
					driverData = r3e.data.DriverData.map(this.formatDriverData).filter(
						this.filterDriverDataQualy
					);
				}
				driverData = driverData.sort(this.sortByLapDistance);

				const playerPosition = this.getPlayerPosition(driverData);

				// Start based on user with offset so it can wrap around
				// const start = Math.max(playerPosition, 0) + driverData.length;
				// const start = driverData.length;

				// Make sure these are unique otherwise it will "loop" with few opponents
				const dataLength = driverData.length;
				const aheadCount = dataLength - (dataLength - playerPosition);
				const start = aheadCount >= eDriverNum
					?	playerPosition - eDriverNum
					:	playerPosition - aheadCount;
				const end = aheadCount >= eDriverNum
					?	playerPosition + (eDriverNum + 1)
					:	playerPosition + (((eDriverNum * 2) + 1) - aheadCount);

				driverData = uniq(
					driverData
					.slice(start, end)
				);
				/*driverData = uniq(
					driverData
						.concat(driverData, driverData)
						.slice(4, 11)
				);*/
			}

			this.calculateDiffs(driverData);

			let driverDataAhead = driverData.slice().reverse();
			let driverDataBehind = driverData.slice().reverse();

			if (this.props.relative) {
				const playerPosition = this.getPlayerPosition(driverDataAhead);
				driverDataAhead = driverDataAhead.slice(
					playerPosition + 1, playerPosition + (eDriverNum + 1)
				);
				driverDataBehind = driverDataBehind.slice(
					Math.max(0, playerPosition - eDriverNum),
					playerPosition
				);
				driverDataBehind.reverse();
			}

			if (!this.props.relative) {
				const playerPosition = this.getPlayerPosition(driverData);

				driverData = this.vrGame
					?	driverData.slice(
							Math.max(playerPosition - 5, 0),
							playerPosition + 6
						)
					:	driverData.slice(
							Math.max(playerPosition - 6, 0),
							playerPosition + 7
						);
			}

			this.getPlayerPosition(driverData);
			this.drivers = driverData.map((driver) => {
				delete driver.meta;
				return driver;
			});

			this.driversAhead = driverDataAhead.map((driver) => {
				delete driver.meta;
				return driver;
			});
			this.driversBehind = driverDataBehind.map((driver) => {
				delete driver.meta;
				return driver;
			});
			this.sessionPhase = r3e.data.SessionPhase;
			this.currentLap = r3e.data.CompletedLaps + 1;
			this.maxLaps = r3e.data.NumberOfLaps;

			this.position = r3e.data.Position;
			this.positionClass = r3e.data.PositionClass;
			this.sessionType = r3e.data.SessionType;
			this.actualRoundsLeft =
				eRoundsLeft === 0
				?	eRoundsLeft + 1
				:	eRoundsLeft;
		}
		this.sessionTimeRemaining = r3e.data.SessionTimeRemaining;
		this.lapTimeCurrentSelf = r3e.data.LapTimeCurrentSelf;
		if (!this.props.relative) {
			this.vrGame = this.props.settings.subSettings.shortBar.enabled;
		}
	};

	private getPlayerPosition = (driverData: IDriverInfo[]) => {
		let userPosition = 0;
		const driverDataLength = driverData.length;
		for (let i = 0; i < driverDataLength; i++) {
			if (driverData[i].id === r3e.data.VehicleInfo.SlotId) {
				userPosition = i;
			}
		}

		return userPosition;
	};

	private filterDriverDataQualy = (driver: IDriverInfo) => {
		const isRaceSession = r3e.data.SessionType === ESession.Race;
		if (
			!isRaceSession &&
			driver.meta.InPitlane === 1 &&
			!driver.isUser
		) {
			return false;
		}
		if (
			isRaceSession && driver.finishStatus > 1
		) {
			return false;
		}
		return true;
	};

	private filterDriverData = (driver: IDriverInfo) => {
		const isRaceSession = r3e.data.SessionType === ESession.Race;
		if (
			!isRaceSession &&
			driver.meta.SectorTimeBestSelf.Sector3 === INVALID &&
			!driver.isUser
		) {
			return false;
		}
		return true;
	};

	private formatDriverData = (driver: IDriverData): IDriverInfo => {
		const isUser =
			r3e.data!.VehicleInfo.SlotId === driver.DriverInfo.SlotId;
		if (
			driver.DriverInfo.ClassPerformanceIndex ===
			r3e.data.VehicleInfo.ClassPerformanceIndex
		) {
			this.classDriverCount += 1;
		} else {
			this.multiClass = true;
		}

		const driverData = {
			isUser,
			id: driver.DriverInfo.SlotId,
			modelId: driver.DriverInfo.ModelId,
			name: base64ToString(driver.DriverInfo.Name),
			position: driver.Place,
			positionClass: driver.PlaceClass,
			meta: driver,
			lapDiff: driver.CompletedLaps - r3e.data.CompletedLaps,
			diff: isUser ? this.getPlayerPositionText() : '',
			classColor: getClassColor(driver.DriverInfo.ClassPerformanceIndex),
			carPerformance: driver.DriverInfo.ClassPerformanceIndex,
			carClassId: driver.DriverInfo.ClassId,
			carClassName: getCarClassName(driver.DriverInfo.ClassId),
			inPit: driver.InPitlane,
			mandatoryPit: driver.PitStopStatus,
			numStops: driver.NumPitstops,
			validLap: driver.CurrentLapValid,
			currentTime: driver.LapTimeCurrentSelf,
			gapToPlayer: isUser ? 0 : this.getGapToPlayer(driver.LapDistance),
			finishStatus: driver.FinishStatus,
			userId: driver.DriverInfo.UserId,
			lapDistance: driver.LapDistance,
			logoUrl: this.logoUrlp1 +
				driver.DriverInfo.ManufacturerId.toString() +
				this.logoUrlp2,
			classUrl: this.logoUrlp1 +
				driver.DriverInfo.ClassId.toString() +
				this.logoUrlp2
		};
		this.userDriverData = driverData;
		return driverData;
	};

	private forceClassColorUpdate() {
		r3e.data.DriverData.forEach((driver) => {
			getClassColor(driver.DriverInfo.ClassPerformanceIndex);
		});
	}

	private calculateDiffs(drivers: IDriverInfo[]) {
		const isRace = r3e.data.SessionType === ESession.Race;

		isRace
			? this.props.relative
				? this.calculateDiffsRaceRelative(drivers)
				: this.calculateDiffsRace(drivers)
			: this.props.relative
			&& this.props.settings.subSettings.showAllSessions.enabled
				? this.calculateDiffsRaceRelative(drivers)
				: this.calculateDiffsQualify(drivers);
	}

	private calculateDiffsQualify(drivers: IDriverInfo[]) {
		const userBestSector =
			r3e.data.SectorTimesBestSelf.Sector3 !== INVALID
				? r3e.data.SectorTimesBestSelf.Sector3
				: 0;

		drivers.forEach((driver, i) => {
			if (driver.isUser) {
				this.playerPosition = i + 1;
				return;
			}
			const diff =
				driver.meta.SectorTimeBestSelf.Sector3 - userBestSector;
			driver.diff =
				diff > 60
					? formatTime(diff, 'm:ss.SSS', true)
					: formatTime(diff, 's.SSS', true);
		});
	}

	private calculateDiffsRace(drivers: IDriverInfo[]) {
		const driversInfront = drivers.slice(0, r3e.data.Position - 1);
		let infrontDiff = 0;
		driversInfront.reverse().forEach((driver) => {
			infrontDiff += driver.meta.TimeDeltaBehind;
			let gotLapped = false;
			if (
				driver.lapDiff === 1 &&
				driver.lapDistance > r3e.data.LapDistance
			) {
				gotLapped = true;
				driver.diff = `-${Math.abs(driver.lapDiff)} ${_(
								'lap'
						  )}`;
			}
			if (
				driver.lapDiff > 1
			) {
				gotLapped = true;
				if (
					driver.lapDistance > r3e.data.LapDistance
				) {
					driver.diff = `-${Math.abs(driver.lapDiff)} ${_(
									'laps'
							)}`;
				} else {
					const theLapDiff = driver.lapDiff - 1;
					driver.diff = theLapDiff > 1
						?	(driver.diff = `-${Math.abs(theLapDiff)} ${_(
								'laps'
							)}`)
						:	(driver.diff = `-${Math.abs(theLapDiff)} ${_(
								'lap'
							)}`);
				}
			}

			if (!gotLapped) {
				driver.diff =
					infrontDiff > 60
						? formatTime(infrontDiff * -1, 'm:ss.SSS')
						: formatTime(infrontDiff * -1, 's.SSS');
			}
		});

		const driversAfter = drivers.slice(
			r3e.data.Position,
			r3e.data.DriverData.length
		);
		let afterDiff = 0;
		driversAfter.forEach((driver) => {
			afterDiff += driver.meta.TimeDeltaFront;
			let gotLapped = false;
			if (
				driver.lapDiff === -1 &&
				driver.lapDistance < r3e.data.LapDistance
			) {
				gotLapped = true;
				driver.diff = `+${Math.abs(driver.lapDiff)} ${_(
								'lap'
						  )}`;
			}
			if (
				driver.lapDiff < -1
			) {
				gotLapped = true;
				if (
					driver.lapDistance < r3e.data.LapDistance
				) {
					driver.diff = `+${Math.abs(driver.lapDiff)} ${_(
									'laps'
							)}`;
				} else {
					const theLapDiff = driver.lapDiff + 1;
					driver.diff = theLapDiff < -1
						?	`+${Math.abs(theLapDiff)} ${_(
								'laps'
							)}`
						:	`+${Math.abs(theLapDiff)} ${_(
								'lap'
							)}`;
				}
			}
			if (!gotLapped) {
				driver.diff =
					afterDiff > 60
						? formatTime(afterDiff, 'm:ss.SSS')
						: formatTime(afterDiff, 's.SSS');
			}
		});
		this.playerPosition = r3e.data.Position;
	}

	private calculateDiffsRaceRelative(drivers: IDriverInfo[]) {
		/* let userPosition = 0;
		drivers.forEach((driver, i) => {
			if (driver.meta.DriverInfo.SlotId === r3e.data.VehicleInfo.SlotId) {
				userPosition = i;
			}
		}); */
		const userLapDistance = r3e.data.LapDistance;
		drivers.forEach((driver) => {
			if (driver.isUser) {
				driver.diff = '';
				return;
			}

			let diff = userLapDistance - driver.meta.LapDistance;
			if (diff < -(r3e.data.LayoutLength / 2)) {
				diff = diff + r3e.data.LayoutLength;
			}
			if (diff > (r3e.data.LayoutLength / 2)) {
				diff = diff - r3e.data.LayoutLength;
			}
			diff = diff;
			if (diff < 0) {
				this.playersAhead += 1;
			}
			if (diff > 0) {
				this.playersBehind += 1;
			}
			/*if (diff < 0 && i > userPosition) {
				diff =
					userLapTime -
					(driver.meta.LapDistance - r3e.data.LayoutLength);
			}

			if (diff > 0 && i < userPosition) {
				diff =
					userLapTime -
					(driver.meta.LapDistance + r3e.data.LayoutLength);
			}

			const playerSpeed = r3e.data.CarSpeed.toString().indexOf('E') > -1
				? 0
				: r3e.data.CarSpeed;

			const opponentSpeed = driver.meta.CarSpeed.toString().indexOf('E') > -1
				? 0
				: driver.meta.CarSpeed; */

			const bestLapSelf =
				personalBestTime > -1
					?	personalBestTime
					:	r3e.data.LapTimeBestSelf > -1
							? r3e.data.LapTimeBestSelf
							: 0;

			const bestLapClass =
				r3e.data.LapTimeBestLeaderClass > -1
				? r3e.data.LapTimeBestLeaderClass
				: 0;

			const bestLapLeader =
				r3e.data.LapTimeBestLeader > -1
				? r3e.data.LapTimeBestLeader
				: 0;

			const bestLapOpponent =
				driver.meta.SectorTimeBestSelf.Sector3 > 0
				? driver.meta.SectorTimeBestSelf.Sector3
				: 0;

			const avgLapSpeedOpponent =
				bestLapOpponent > 0
				? r3e.data.LayoutLength / bestLapOpponent
				: driver.meta.DriverInfo.ClassPerformanceIndex ===
					r3e.data.VehicleInfo.ClassPerformanceIndex &&
					bestLapClass > 0
					? r3e.data.LayoutLength / bestLapClass
					: bestLapLeader > 0
						? r3e.data.LayoutLength / bestLapLeader
						: 40;

			const avgLapSpeed =
				bestLapSelf > 0
				? r3e.data.LayoutLength / bestLapSelf
				: bestLapClass > 0
					? r3e.data.LayoutLength / bestLapClass
					: bestLapLeader > 0
						? r3e.data.LayoutLength / bestLapLeader
						: 40;

			const avgSpeed =
			(
				avgLapSpeed + avgLapSpeedOpponent
			) / 2;

			const timeDiff = fancyTimeFormatGap((diff / avgSpeed), 1, 1);

			const prefix = diff > 0 ? '+' : '';
			driver.diff = this.props.settings.subSettings.showGapsInSeconds.enabled
				? `${prefix}${timeDiff}`
				: `${prefix}${diff.toFixed(0)}m`;
		});
	}

	private getGapToPlayer(lapDist: number): number {
		if (lapDist === INVALID) {
			return 0;
		}
		const trackDist = r3e.data.LayoutLength;
		const playerDist = r3e.data.LapDistance;
		let distToPlayer = playerDist - lapDist;

		if (distToPlayer < -(trackDist / 2)) {
			distToPlayer = distToPlayer + trackDist;
		}
		if (distToPlayer > (trackDist / 2)) {
			distToPlayer = distToPlayer - trackDist;
		}
		return parseInt((distToPlayer * -1).toFixed(0), 10);
	}

	private getPlayerPositionText(): string {
		const isntRace = r3e.data.SessionType !== ESession.Race;
		if (isntRace) {
			const bestTime = r3e.data.SectorTimesBestSelf.Sector3;
			return bestTime !== INVALID
				? bestTime > 60
					? formatTime(Math.max(0, bestTime), 'm:ss.SSS')
					: formatTime(Math.max(0, bestTime), 's.SSS')
				: '-';
		}

		const lapTime = r3e.data.LapTimeCurrentSelf;
		return lapTime !== INVALID
			? lapTime > 60
				? formatTime(Math.max(0, lapTime), 'm:ss.SSS')
				: formatTime(Math.max(0, lapTime), 's.SSS')
			: '-';
	}

	render() {
		const playerIsAlone = this.playerCount === 1;
		if (playerIsAlone && this.props.relative && !showAllMode) {
			return null;
		}

		/*const willOverlapPitMenu =
			this.props.relative && this.pitState === EPitState.Pitting;
		if (willOverlapPitMenu && !showAllMode) {
			return null;
		}*/

		const onlyShowInRace =
			this.props.relative
			&& this.sessionType !== ESession.Race
			&& !this.props.settings.subSettings.showAllSessions.enabled;
		if (onlyShowInRace && !showAllMode) {
			return null;
		}

		const notInRacePhase = this.sessionPhase < ESessionPhase.Countdown;
		if (notInRacePhase && !showAllMode) {
			return null;
		}

		const positionOffset = this.vrGame
			?	6 - this.playerPosition
			:	7 - this.playerPosition;

		let sessionName = '';
		switch (this.sessionType) {
			case 0:
				sessionName = _('Practice');
				break;
			case 1:
				sessionName = _('Qualification');
				break;
			case 2:
				sessionName = _('Race');
				break;
			case 3:
				sessionName = _('Warmup');
				break;
		}
		return (
			<div
				className={classNames(
					'positionBarContainer',
					this.props.relative ? 'relative' : 'normal',
					{
						shouldShow:
							!!this.drivers.length || showAllMode,
						showCarsRelative:
							this.props.relative &&
							this.props.settings.subSettings.showCarNames.enabled &&
							!this.props.settings.subSettings.showCarLogos.enabled,
						showPitstops:
							this.props.relative &&
							(this.sessionType === ESession.Race || showAllMode) &&
							this.props.settings.subSettings
							.showPitStops.enabled,
						gameIsVR: this.vrGame,
						noStandings:
							!this.props.relative &&
							!this.props.settings.subSettings.showStandings.enabled,
						noRace: this.sessionType !== ESession.Race
					}
				)}
				{...widgetSettings(this.props)}
			>
				{
					(
						this.props.relative ||
						(
							!this.props.relative &&
							this.props.settings.subSettings.showStandings.enabled
						)
					) &&
					this.sessionPhase !== INVALID && (
					<div
						className={classNames(
							style.positionBar,
							this.props.className,
							{
								gameIsVR: this.vrGame
							}
						)}
					>
						{
							times(!this.props.relative ? positionOffset : 0).map(
								(i) => {
									return (
										<div
											key={`empty-${i}`}
											className="player"
										/>
									);
								}
							)
						}
						{this.drivers.map((player, i) => {
							return (
								<PositionEntry
									key={`${player.id}-${i}`}
									player={player}
									relative={this.props.relative}
									settings={this.props.settings}
								/>
							);
						})}
					</div>
				)}

				{!this.props.relative &&
					this.props.settings.subSettings.lapTime.enabled && (
						<div className="currentLapTime">
							<span className="mono">
								{this.lapTimeCurrentSelf !== INVALID
									? formatTime(
											this.lapTimeCurrentSelf,
											'mm:ss.SSS'
									  )
									: '-:--.---'}
							</span>
							<div className="label">{_('Lap time')}</div>
						</div>
					)}

				{!this.props.relative &&
					this.props.settings.subSettings.currentPosition.enabled &&
					(showAllMode || (
						this.position !== INVALID &&
						this.sessionType === ESession.Race
					)) && (
						<div
							className={classNames(
								'currentPosition',
								{
									gameIsVR: this.vrGame
								}
							)}
							style={{
								left:
									this.props.settings.subSettings.lapTime.enabled
									?	'160px'
									:	'10px'
							}}
						>
							<span className="mono">
								{
									showAllMode
									?	'9/12'
									:	`${this.position}/${r3e.data.DriverData.length}`
								}
							</span>
							<div className="label">{_('Position')}</div>
						</div>
					)}

				{!this.props.relative &&
					this.props.settings.subSettings.currentPosition.enabled &&
					(
						(
							this.positionClass !== INVALID &&
							this.multiClass &&
							this.sessionType === ESession.Race
						) ||
						showAllMode
					) && (
						<div
							className={classNames(
								'currentPositionClass',
								{
									gameIsVR: this.vrGame
								}
							)}
							style={{
								left:
									this.props.settings.subSettings.lapTime.enabled
									?	'270px'
									:	'120px'
							}}
						>
							<span className="mono">
								{
									showAllMode
									?	'3/6'
									:	`${this.positionClass}/${this.classDriverCount}`
								}
							</span>
							<div className="label">{_('Position Class')}</div>
						</div>
					)}

				{!this.props.relative &&
					this.props.settings.subSettings.currentLap.enabled &&
					this.maxLaps !== INVALID && (
						<div className="currentLap">
							<span className="mono">
								{this.currentLap}/{this.maxLaps}
							</span>
							<div className="label">{_('Lap')}</div>
						</div>
					)}

				{!this.props.relative &&
					this.props.settings.subSettings.sessionLaps.enabled &&
					(
						(
							r3e.data.CompletedLaps > 0 &&
							this.maxLaps === INVALID
						) || showAllMode
					) && (
					<div
						className="sessionLaps"
						style={{
							left:
								this.props.settings.subSettings.lapTime.enabled
								?	this.props.settings.subSettings.currentPosition.enabled
									?	'380px'
									:	'160px'
								:	this.props.settings.subSettings.currentPosition.enabled
									?	'230px'
									:	'10px'
						}}
					>
						<span className="mono">
							{
								showAllMode
								?	6
								:	r3e.data.CompletedLaps
							}
						</span>
						<div className="label">
							{_('Completed Laps')}
						</div>
					</div>
					)}

				{
					!this.props.relative &&
					(
						(
							this.maxLaps === INVALID &&
							this.isPlayer &&
							(
								(
									this.props.settings.subSettings.sessionLapsRemain.enabled &&
									this.actualRoundsLeft > -1
								) ||
								(
									this.props.settings.subSettings.sessionLapsTotal.enabled &&
									personalBestTime > 0
								) ||
								(
									this.props.settings.subSettings.sessionLapsRemain.enabled &&
									this.actualRoundsLeft > -1 &&
									this.props.settings.subSettings.sessionLapsTotal.enabled &&
									personalBestTime > 0
								)
							) &&
							(
								(
									this.props.settings.subSettings.sessionLapsTotal.enabled &&
									personalBestTime > 0
								) ||
								(
									this.props.settings.subSettings.sessionLapsRemain.enabled &&
									this.actualRoundsLeft > -1
								)
							)
						) ||
						showAllMode
					) && (
					<div
						className="sessionLapsRemain"
						style={{
							left:
								this.props.settings.subSettings.lapTime.enabled
								?	this.props.settings.subSettings.currentPosition.enabled
									?	this.props.settings.subSettings.sessionLaps.enabled
										?	'490px'
										:	'380px'
									:	this.props.settings.subSettings.sessionLaps.enabled
										?	'270px'
										:	'160px'
								:	this.props.settings.subSettings.currentPosition.enabled
									?	this.props.settings.subSettings.sessionLaps.enabled
										?	'340px'
										:	'230px'
									:	this.props.settings.subSettings.sessionLaps.enabled
										?	'120px'
										:	'10px'
						}}
					>
						<span className="mono">
							{
								this.props.settings.subSettings.sessionLapsTotal.enabled ||
								this.props.settings.subSettings.sessionLapsRemain.enabled
								?	this.props.settings.subSettings.sessionLapsTotal.enabled &&
									(personalBestTime > 0 || showAllMode) &&
									this.props.settings.subSettings.sessionLapsRemain.enabled &&
									(this.actualRoundsLeft > -1 || showAllMode)
									?	`${
											showAllMode
											?	6
											:	this.actualRoundsLeft
										} / ${
											showAllMode
											?	12
											:	Math.floor(r3e.data.SessionTimeDuration / personalBestTime)
										}`
									:	this.props.settings.subSettings.sessionLapsTotal.enabled &&
										(personalBestTime > 0 || showAllMode)
										?	`${
												showAllMode
												?	12
												:	Math.floor(r3e.data.SessionTimeDuration / personalBestTime)
											}`
										:	`${
												showAllMode
												?	6
												:	this.actualRoundsLeft
											}`
									:	''
							}
						</span>
						<div className="label">
							{
								this.props.settings.subSettings.sessionLapsRemain.enabled ||
								this.props.settings.subSettings.sessionLapsTotal.enabled
								?	this.props.settings.subSettings.sessionLapsTotal.enabled &&
									(personalBestTime > 0 || showAllMode)
									?	_('Estimated Laps')
									:	_('Estimated Laps left')
								:	''
							}
						</div>
					</div>
					)}

				{!this.props.relative &&
					this.props.settings.subSettings.sessionTime.enabled &&
					this.sessionTimeRemaining !== INVALID && (
						<div className="sessionTime">
							<span className="mono">
								{
									showAllMode
									?	'01:21:33'
									:	formatTime(
											this.sessionTimeRemaining,
											'H:mm:ss'
										)
								}
							</span>
							<div className="label">{sessionName}</div>
						</div>
					)}
			</div>
		);
	}
}

interface IEntryProps extends React.HTMLAttributes<HTMLDivElement> {
	player: IDriverInfo;
	relative: boolean;
	settings: IWidgetSetting;
}

@observer
export class PositionEntry extends React.Component<IEntryProps, {}> {
	constructor(props: IEntryProps) {
		super(props);
	}
	render() {
		const player = this.props.player;
		if (
			this.props.relative ||
			(
				!this.props.relative &&
				this.props.settings.subSettings.showStandings.enabled
			)
		) {
		return (
			<div
				className={classNames('player', {
					isUser: player.isUser,
					lapping: player.lapDiff < 0,
					sameLap: player.lapDiff === 0,
					lapped: player.lapDiff > 0,
					validLap: player.validLap > 0 && player.currentTime > 0,
					noValidLap: player.validLap < 1,
					noRaceSession: showAllMode || r3e.data.SessionType !== ESession.Race,
					showCars: this.props.relative &&
						this.props.settings.subSettings.showCarNames.enabled &&
						!this.props.settings.subSettings.showCarLogos.enabled,
					showLogos: this.props.relative &&
						this.props.settings.subSettings.showCarLogos.enabled,
					showClassLogos: this.props.relative &&
						this.props.settings.subSettings.showClassLogos.enabled
				})}
			>
				<div
					className="position"
					style={{
						color: this.props.relative
							? '#fff'
							: undefined
					}}
				>
					{player.positionClass}
				</div>{' '}
				<div className="name">
					{
						this.props.relative
						?	(
								this.props.settings.subSettings.showCarNames.enabled ||
								this.props.settings.subSettings.showCarLogos.enabled
							)
								?	getInitials(player.name)
								:	player.name
						:	this.props.settings.subSettings.showStandings.enabled
							?	player.name
							:	''

					}
				</div>
				{this.props.relative &&
					this.props.settings.subSettings
					.showCarNames.enabled &&
					!this.props.settings.subSettings
					.showCarLogos.enabled && (
						<div className="carName">{getCarName(player.modelId)}</div>
				)}
				{this.props.relative &&
					this.props.settings.subSettings
					.showCarLogos.enabled && (
						<div className="carLogo">
							<img src={player.logoUrl} width="18" height="18"/>
						</div>
				)}
				{
					(
						this.props.relative ||
						(
							!this.props.relative &&
							this.props.settings.subSettings.showStandings.enabled
						)
					) && (
						<div className="diff mono">{player.diff}</div>
					)
				}
				{' '}
				{
					this.props.relative &&
					(r3e.data.SessionType === ESession.Race || showAllMode) &&
					this.props.settings.subSettings.showPitStops.enabled && (
						<div
							className={classNames('stopStatus', {
								textShadow: player.numStops > 0
							})}
							style={{
								background:
									player.mandatoryPit === 1
										?	'green'
										:	player.mandatoryPit === 0
											?	'red'
											:	player.inPit
												? 	'red'
												:	'dimgray',
								color:
									player.numStops > 0
										?	'white'
										:	player.inPit
											?	'white'
											:	player.mandatoryPit === 1
												?	'green'
												:	player.mandatoryPit === 0
													?	'red'
													:	player.inPit
														? 	'red'
														:	'dimgray'
							}}
						>
							{
								player.inPit
								?	'PIT'
								:	player.numStops
							}
						</div>
					)
				}
				{this.props.relative &&
					player.isUser &&
					this.props.settings.subSettings.showUserId.enabled && (
						<div className="userId">{player.userId}</div>
				)}
				<div
					className="classStyle"
					style={{
						borderTop: !this.props.relative
							? `3px solid ${player.classColor}`
							: undefined,
						borderLeft: this.props.relative
							? `4px solid ${player.classColor}`
							: undefined
					}}
				/>
				{
					this.props.relative &&
					this.props.settings.subSettings.showClassLogos.enabled && (
						<div className="classLogo">
							<img src={player.classUrl} width="20" height="20"/>
						</div>
					)
				}
			</div>
		);
		}
		return null;
	}
}
