import {
	classNames,
	mpsToKph,
	widgetSettings
} from './../../lib/utils';
import { action, observable } from 'mobx';
import { IWidgetSetting, lowPerformanceMode, showAllMode } from '../app/app';
import { observer } from 'mobx-react';
import _ from './../../translate';
import r3e, {
	registerUpdate,
	unregisterUpdate,
	nowCheck
} from '../../lib/r3e';
import React from 'react';
import style from './pitLimiter.scss';

interface IProps extends React.HTMLAttributes<HTMLDivElement> {
	settings: IWidgetSetting;
}

@observer
export default class PitLimiter extends React.Component<IProps, {}> {
	@observable
	inPitLane = 0;

	@observable
	pitlaneMax = 0;

	@observable
	speed = 0;

	@observable
	lastCheck = 0;

	constructor(props: IProps) {
		super(props);

		registerUpdate(this.update);
	}

	componentWillUnmount() {
		unregisterUpdate(this.update);
	}

	@action
	private update = () => {
		if (
			(
				!lowPerformanceMode &&
				nowCheck - this.lastCheck >= 66.66
			) ||
			nowCheck - this.lastCheck >= 133.32
		) {
			this.lastCheck = nowCheck;
			this.inPitLane = r3e.data.InPitlane;
			if (this.inPitLane) {
				this.speed = mpsToKph(r3e.data.CarSpeed);
				this.pitlaneMax = mpsToKph(r3e.data.SessionPitSpeedLimit);
			}
		}
	};

	render() {
		if (this.inPitLane !== 1 && !showAllMode) {
			return null;
		}
		return (
			<div
				{...widgetSettings(this.props)}
				className={classNames(style.pitLimiter, this.props.className, {
					warning: showAllMode || this.speed > this.pitlaneMax + 1,
					shouldShow: showAllMode || (this.inPitLane === 1 && this.speed > 10)
				})}
			>
				<div className="max">
					{_('In Pit Lane')}:{' '}
					<span className="mono">{this.pitlaneMax.toFixed(0)}</span>{' '}
					{_('Kph')}
				</div>
				<div className="current">
					{_('Current speed')}:{' '}
					<span className="mono">{showAllMode
						?	(this.pitlaneMax + 1).toFixed(0)
						:	this.speed.toFixed(0)}</span>{' '}
					{_('Kph')}
				</div>
			</div>
		);
	}
}
