import {
	classNames,
	widgetSettings,
	getTrackName,
	getLayoutName,
	// showDebugMessage,
	getCornerName
} from './../../lib/utils';
import {
	IWidgetSetting,
	lowPerformanceMode
} from '../app/app';
import { action, observable } from 'mobx';
import { observer } from 'mobx-react';
import _ from './../../translate';
import r3e, {
	registerUpdate,
	unregisterUpdate,
	nowCheck
} from './../../lib/r3e';
import React from 'react';
import style from './cornerNames.scss';

interface IProps extends React.HTMLAttributes<HTMLDivElement> {
	settings: IWidgetSetting;
}

@observer
export default class CornerNames extends React.Component<IProps, {}> {
	@observable
	trackName = '';

	@observable
	trackLayoutName = '';

	@observable
	trackLength = 0;

	@observable
	trackDist = 0;

	@observable
	cornerName = '';

	@observable
	details = true;

	@observable
	corners = true;

	@observable
	lastCheck = 0;

	constructor(props: IProps) {
		super(props);

		registerUpdate(this.update);
	}

	componentWillUnmount() {
		unregisterUpdate(this.update);
	}

	@action
	private update = () => {
		this.details =
			this.props.settings.subSettings.trackDetails.enabled;
		this.corners =
			this.props.settings.subSettings.corners.enabled;

		if (
			(
				!lowPerformanceMode &&
				nowCheck - this.lastCheck >= 66.66
			) ||
			nowCheck - this.lastCheck >= 133.32
		) {
			this.lastCheck = nowCheck;

			if (
				this.details ||
				this.corners
			) {
				this.trackDist = Math.round(r3e.data.LapDistance);
			}

			if (
				this.details &&
				this.trackName === ''
			) {
					this.trackName = getTrackName(r3e.data.LayoutId);
					this.trackLayoutName = getLayoutName(r3e.data.LayoutId);
					this.trackLength = Math.round(r3e.data.LayoutLength);
			}

			if (
				this.corners
			) {
				this.cornerName = r3e.data.InPitlane === 1
					?	'PIT-LANE'
					:	getCornerName(r3e.data.LayoutId, this.trackDist);
			}
		}
	}

	render() {
		if (!this.details && !this.corners) {
			return null;
		}

		return (
			<div
				className={classNames(style.cornerNamesContainer, this.props.className, {
					showDetails: this.details && !this.corners,
					showCorners: this.corners && !this.details,
					showBoth: this.details && this.corners,
					theSillyShit:
						this.props.settings.subSettings.noColors.enabled
				})}
				style={{
					width: '410px',
					height: this.details && this.corners
						?	'110px'
						:	!this.details && this.corners ||
							this.details && !this.corners
							?	'55px'
							:	'0px'
				}}
				{...widgetSettings(this.props)}
			>
				{
					this.details && (
						<div className="trackNameContainer">
							<div className="trackNameText">
								{this.trackName}
							</div>
						</div>
					)
				}
				{
					this.details && (
						<div className="layoutNameContainer">
							<div className="layoutNameText">
								{this.trackLayoutName}
							</div>
						</div>
					)
				}
				{
					this.details && (
						<div className="trackLengthTextContainer">
							<div className="trackLengthText">
								{_('Length:')}
							</div>
						</div>
					)
				}
				{
					this.details && (
						<div className="trackLengthContainer">
							<div className="trackLengthNumber">
								{this.trackLength}{' '}{_('M')}{' '}
							</div>
						</div>
					)
				}
				{
					this.details && (
						<div className="trackDistTextContainer">
							<div className="trackDistText">
								{_('Dist:')}
							</div>
						</div>
					)
				}
				{
					this.details && (
						<div className="trackDistContainer">
							<div className="trackDistNumber">
								{this.trackDist}{' '}{_('M')}{' '}
							</div>
						</div>
					)
				}
				{
					this.corners && (
						<div className="cornerNameContainer">
							<div className="cornerNameText">
								{this.cornerName}
							</div>
						</div>
					)
				}
			</div>
		);
	}
}
