import {
	classNames,
	INVALID,
	mpsToKph,
	mpsToMph,
	rpsToRpm,
	widgetSettings
} from './../../lib/utils';
import { action, observable } from 'mobx';
import { EEngineType } from './../../types/r3eTypes';
import { IWidgetSetting, lowPerformanceMode, showAllMode } from '../app/app';
import { observer } from 'mobx-react';
import r3e, {
	registerUpdate,
	unregisterUpdate,
	nowCheck
} from './../../lib/r3e';
import React from 'react';
import style from './motec.scss';

interface IProps extends React.HTMLAttributes<HTMLDivElement> {
	settings: IWidgetSetting;
}

@observer
export default class Motec extends React.Component<IProps, {}> {
	@observable
	speed = INVALID;

	@observable
	rpm = 0;

	@observable
	maxRpm = 0;

	@observable
	upshiftRps = 0;

	@observable
	gear = 0;

	@observable
	limiter = 0;

	gearNameLookup = {};

	@observable
	lastCheck = 0;

	constructor(props: IProps) {
		super(props);

		registerUpdate(this.update);

		const isElectric =
			r3e.data.VehicleInfo.EngineType === EEngineType.Electric;

		this.gearNameLookup = isElectric
			? {
					'-1': 'R',
					0: 'N',
					1: 'D',
					2: 'S'
			  }
			: {
					'-1': 'R',
					0: 'N'
			  };
	}

	componentWillUnmount() {
		unregisterUpdate(this.update);
	}

	@action
	private update = () => {
		if (
			(
				!lowPerformanceMode &&
				nowCheck - this.lastCheck >= 33.33
			) ||
			nowCheck - this.lastCheck >= 66.66
		) {
			this.lastCheck = nowCheck;
			this.speed = r3e.data.CarSpeed;
			this.rpm = rpsToRpm(r3e.data.EngineRps);
			this.maxRpm = rpsToRpm(r3e.data.MaxEngineRps);
			this.upshiftRps = rpsToRpm(r3e.data.UpshiftRps);
			this.gear = r3e.data.Gear;
			this.limiter = r3e.data.PitLimiter;
			this.lastCheck = nowCheck;
		}
	};

	render() {
		const rmpMaxed = this.rpm > this.maxRpm * 0.97;
		const rpmOptimum = this.rpm > this.upshiftRps * 0.92;
		return (
			<div
				{...widgetSettings(this.props)}
				className={classNames(style.motec, this.props.className, {
					rmpMaxed,
					rpmOptimum,
					shouldShow: this.speed !== INVALID,
					plBlink:
						this.props.settings.subSettings.plBlink.enabled,
					plEnabled: showAllMode || this.limiter === 1
				})}
			>
				{/* Speed*/}
				<div className="speed mono">
					{showAllMode
						?	65
						:	this.props.settings.subSettings.showMPH.enabled
							? mpsToMph(this.speed).toFixed(0)
							: mpsToKph(this.speed).toFixed(0)}
				</div>

				{/* RPM */}
				<div className="rpm">
					<div
						className="rpmBar"
						style={{
							width: `${showAllMode
								?	50
								:	(this.rpm / this.maxRpm) * 100}%`
						}}
					/>
				</div>

				{/* Gear */}
				<div className="gear mono">
					{showAllMode
						?	2
						:	this.gearNameLookup[this.gear] || this.gear}
				</div>
			</div>
		);
	}
}
